import React from "react"
import PropTypes from "prop-types"
import { makeStyles } from "@material-ui/core/styles"
import TreeView from "@material-ui/lab/TreeView"
import TreeItem from "@material-ui/lab/TreeItem"
import Typography from "@material-ui/core/Typography"
import DescriptionIcon from "@material-ui/icons/Description"
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown"
import ArrowRightIcon from "@material-ui/icons/ArrowRight"
import DateRangeIcon from "@material-ui/icons/DateRange"
import { useStaticQuery, graphql, Link } from "gatsby"

const useTreeItemStyles = makeStyles(theme => ({
  root: {
    color: theme.palette.text.secondary,
    "&:focus > $content": {
      backgroundColor: `var(--tree-view-bg-color, ${theme.palette.grey[400]})`,
      color: "var(--tree-view-color)",
    },
  },
  content: {
    color: theme.palette.text.secondary,
    borderTopRightRadius: theme.spacing(2),
    borderBottomRightRadius: theme.spacing(2),
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    "$expanded > &": {
      fontWeight: theme.typography.fontWeightRegular,
    },
  },
  group: {
    marginLeft: 0,
    "& $content": {
      paddingLeft: theme.spacing(2),
    },
  },
  expanded: {},
  label: {
    fontWeight: "inherit",
    color: "inherit",
  },
  labelRoot: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(2, 0),
  },
  labelIcon: {
    marginRight: theme.spacing(1),
  },
  labelText: {
    fontWeight: "inherit",
    flexGrow: 1,
  },
}))

function StyledTreeItem(props) {
  const classes = useTreeItemStyles()
  const {
    labelText,
    labelIcon: LabelIcon,
    labelInfo,
    color,
    bgColor,
    ...other
  } = props

  return (
    <TreeItem
      label={
        <div className={classes.labelRoot}>
          <LabelIcon color="inherit" className={classes.labelIcon} />
          <Typography variant="body2" className={classes.labelText}>
            {labelText}
          </Typography>
          <Typography variant="caption" color="inherit">
            {labelInfo}
          </Typography>
        </div>
      }
      style={{
        "--tree-view-color": color,
        "--tree-view-bg-color": bgColor,
      }}
      classes={{
        root: classes.root,
        content: classes.content,
        expanded: classes.expanded,
        group: classes.group,
        label: classes.label,
      }}
      {...other}
    />
  )
}

StyledTreeItem.propTypes = {
  bgColor: PropTypes.string,
  color: PropTypes.string,
  labelIcon: PropTypes.elementType.isRequired,
  labelInfo: PropTypes.string,
  labelText: PropTypes.string.isRequired,
}

const useStyles = makeStyles({
  root: {
    height: 264,
    flexGrow: 1,
    maxWidth: 400,
  },
  treeLink: {
    textDecoration: "none",
  },
})

export default function HandoutTreeView() {
  const classes = useStyles()
  const data = useStaticQuery(query)
  const { allMarkdownFile } = data

  return (
    <TreeView
      className={classes.root}
      defaultExpanded={["3"]}
      defaultCollapseIcon={<ArrowDropDownIcon />}
      defaultExpandIcon={<ArrowRightIcon />}
      defaultEndIcon={<div style={{ width: 24 }} />}
    >
      {allMarkdownFile.group.map((week, index) => (
        <StyledTreeItem
          key={index}
          nodeId={`${index}`}
          labelText={week.fieldValue
            .split("/")
            .pop()
            .replace("_", " ")}
          labelIcon={DateRangeIcon}
          labelInfo={`${week.edges.length}`}
          color="#1a73e8"
          bgColor="#e8f0fe"
        >
          {week.edges &&
            week.edges.map(({ node }, index) => (
              <Link
                key={index}
                className={classes.treeLink}
                to={`/handout/${
                  node.absolutePath
                    .split("/")
                    .pop()
                    .split(".")[0]
                }`}
              >
                <StyledTreeItem
                  nodeId={`1${index}`}
                  labelText={node.absolutePath
                    .split("/")
                    .pop()
                    .split(".")[0]
                    .replace(/-/g, " ")}
                  labelIcon={DescriptionIcon}
                  color="#1a73e8"
                  bgColor="#e8f0fe"
                />
              </Link>
            ))}
        </StyledTreeItem>
      ))}
    </TreeView>
  )
}

const query = graphql`
  query NavQuery {
    allMarkdownFile: allFile(filter: { extension: { eq: "md" } }) {
      group(field: dir) {
        edges {
          node {
            absolutePath
          }
        }
        fieldValue
      }
    }
  }
`
