/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useState, useEffect } from "react"
import clsx from "clsx"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import { ThemeProvider } from "@material-ui/styles"
import { light, dark } from "./theme"
import Header from "./header"
import { makeStyles } from "@material-ui/core/styles"
import CssBaseline from "@material-ui/core/CssBaseline"
import { createGlobalStyle } from "styled-components"

const GlobalStyle = createGlobalStyle`
  a {
    color: ${props => (props.light ? "black" : "white")};
  }
  ::selection {
  color: ${props => (props.light ? light.palette.text : dark.palette.text)};
  background: ${props =>
    props.light ? light.palette.primary.light : dark.palette.primary.main};
}
`
const drawerWidth = 240

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: "flex-end",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: 0,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: drawerWidth,
  },
}))

const Layout = ({ children }) => {
  const classes = useStyles()
  const [open, setOpen] = useState(false)
  const [theme, setTheme] = useState("light")

  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  // TODO theme got undefine on first time render
  // only set theme to value got from localStorage once after component mounted
  useEffect(() => {
    setTheme(
      window.localStorage ? localStorage.getItem("theme") : "light" || "light"
    )
  }, [])

  // update theme in localStorage as it changes
  useEffect(() => {
    if (window.localStorage) {
      localStorage.setItem("theme", theme)
    }
  }, [theme])

  const handleDrawerOpen = () => {
    setOpen(true)
  }

  const handleDrawerClose = () => {
    setOpen(false)
  }
  const handleChangeTheme = theme => {
    setTheme(theme)
  }

  return (
    <ThemeProvider theme={theme === "light" ? light : dark}>
      <CssBaseline />
      <GlobalStyle light={theme === "light" ? true : false} />
      <Header
        siteTitle={data.site.siteMetadata.title}
        open={open}
        handleDrawerClose={handleDrawerClose}
        handleDrawerOpen={handleDrawerOpen}
        handleChangeTheme={handleChangeTheme}
      />
      {/* <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `0px 1.0875rem 1.45rem`,
          paddingTop: 0,
        }}
      > */}
      <main
        className={clsx(classes.content, {
          [classes.contentShift]: open,
        })}
      >
        {children}
      </main>
      <footer
        className={clsx(classes.content, {
          [classes.contentShift]: open,
        })}
      >
         {new Date().getFullYear()}, Built by
        {` `}
        <a href="https://xccelerate.co">Xccelerate</a>
      </footer>
      {/* </div> */}
    </ThemeProvider>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
